html {
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}

body {
  background: url("./assets/bg.jpg") no-repeat;
  background-size: cover;
}

#root {
  position: relative;
  width: 100%;
}

.briefIntro {
  margin: 0 auto;
  width: 700px;
  font-weight: 600;
  padding: 25px 25px;
  line-height: 40px;
  font-size: 24px;
  color: #2fa782;
  border-radius: 30px;
  border: 3PX solid #b0eddd;
  background: url("./assets/kuangti.png") no-repeat center;
  background-size: 180% auto;
  //background-position-y: -1118px;
  box-shadow: 0 0 20px 10px #27b577;
  text-indent: 2em;
}

// title部分
.firstTitle {
  width: 348px;
  font-size: 42px;
  background: url("./assets/biaoti.png") no-repeat center;
}

.secTitle {
  width: 700px;
  font-size: 36px;
  background: url("./assets/biaoti2.png") no-repeat center;
}

.thirdTitle {
  width: 523px;
  font-size: 40px;
  background: url("./assets/biaoti3.png") no-repeat center;
}

.title {
  position: relative;
  height: 86px;
  margin: 0 auto;
  margin-top: 30px;
  background-size: 100% 100%;
  color: #fff;
  font-weight: 700;
  line-height: 76px;
  text-align: center;
}

.devCourse {
  margin: 0 auto;
  margin-top: 20px;
  width: 700px;
  font-weight: 600;
  padding: 25px 15px;
  line-height: 40px;
  color: #2fa782;
  border-radius: 30px;
  border: 3PX solid #b0eddd;
  background: url("./assets/kuangti.png") no-repeat center;
  background-size: 112% 106%;
  box-shadow: 0 0 20px 10px #27b577;

  .devCourseItem {
    display: flex;
    align-items: center;
    list-style: none;
    border: 2PX solid #2fa782;
    padding: 15px 0;
    text-align: center;
    margin: 10px 0;
    font-size: 18px;
    height: 140px;

    >img {
      margin: 0 auto;
      display: block;
    }

    .devCourseImg1 {
      width: 479px;
    }

    .devCourseImg2 {
      width: 284px;
    }

    .devCourseItemTitle {
      flex: 0.3;
      font-size: 20px;
      align-items: center;
      font-size: 18px;
    }

    .devCourseItemOne {
      font-size: 18px;
      flex: 0.38;
      text-align: left;

      li {
        line-height: 22px;
        font-size: 18px;
      }
    }

    .devCourseTime {
      height: 100%;
      position: relative;
      flex: 0.32;
      color: #ffc000;
      text-align: left;

      li {
        font-size: 18px;
        line-height: 22px;
      }

      li:nth-child(1) {
        font-size: 60px;
        line-height: 60px;
      }
    }

    .devCourseItemTwo {
      text-align: left;
      flex: 0.24;
      li {
        font-size: 18px;
        line-height: 22px;
      }
    }

    // 平均
    .devCourseAverageTitle {
      flex: 0.25;
      font-size: 26px;
    }

    .devCourseAverage {
      flex: 0.25;
      font-size: 20px;
    }
  }

  .devCourseJustImg {
    display: flex;
    border: none;
    justify-content: space-between;
    margin-top: 30px;

    .devCourseJustImg-left {
      display: flex;
      align-content: center;
      flex-direction: column;
      width: 136px;
      height: 136px;
      background: url("./assets/kuang1.png") no-repeat;
      background-size: cover;
      color: #fff;

      li {
        font-size: 20px;
        line-height: 30px;
      }

      li:nth-child(1) {
        font-size: 28px;
        height: 60px;
        line-height: 60px;
      }
    }

    .devCourseJustImg-right {
      display: flex;
      align-items: center;
      width: 509px;
      height: 136px;
      background: url("./assets/kuang2.png") no-repeat;
      background-size: cover;
      color: #fff;

      li {
        flex: 0.33;
        align-items: center;

        h3 {
          font-size: 28px;
        }

        p {
          font-size: 18px;
          line-height: 22px;
        }
      }

      li:nth-child(1) {
        font-size: 36px;
      }
    }
  }

  // 期望
  .supposeItem {
    position: relative;
    padding: 0px 15px;
    margin-bottom: 35px;
    font-size: 24px;

    .flower7 {
      position: absolute;
      right: -43px;
      top: -30px;
      width: 97px;
    }

    h3 {
      line-height: 40px;
      color: #ffc000;
    }

    p {
      line-height: 40px;
      color: #2fa782;
    }
  }

  .supposeTitle {
    padding-left: 15px;
    margin-bottom: 40px;
  }

  // 工作城市
  .workCity {
    float: left;
    width: 194px;
    height: 182px;
    text-align: center;
    font-size: 28px;
    font-weight: 700;

    img {
      width: 100%;
      display: block;
    }
  }

  // 职位
  .workCity1 {
    float: left;
    width: 194px;
    height: 240px;
    text-align: center;
    font-size: 20px;
    font-weight: 700;

    img {
      width: 100%;
      display: block;
    }

    .personImg {
      width: 160px;
      margin: 0 auto;
    }
  }

  .devCourseTop {
    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
    justify-content: space-between;

    .workCity1 {
      width: 33.33%;
    }
  }

  .devCourseBtm {
    width: 80%;
    margin: 0 auto;
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
    .workCity1 {
      width: 50%;
    }
  }

  // 虚位以待
  .workTitle {
    position: relative;
    width: 650px;
    height: 40px;
    margin: 0 auto;
    padding: 10px 0;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .workTitleLine {
      width: 100%;
      height: 4px;
      background-color: #ffc000;
    }

    .workTitleTxt {
      position: absolute;
      text-align: center;
      font-size: 30px;
      color: #ffc000;
      font-weight: 700;
      padding: 0 10px;
      background-color: #fff;
    }
  }

  // 招聘方式
  .phoneWay {
    max-width: 550px;
    font-size: 22px;
    line-height: 40px;
    font-weight: 700;
    text-align: center;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;

    span {
      color: #ffc000;
    }
  }

  .qrCode {
    display: block;
    width: 267px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 30px;
  }
}

.logo {
  display: block;
  width: 208px;
  margin: 0 auto;
  margin-top: 60px;
  padding-bottom: 60px;
}

.flower1 {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 242px;
  display: block;
}

.flower2 {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 242px;
  display: block;
}

.flower3 {
  position: absolute;
  left: -200px;
  top: 20px;
  width: 141px;
  display: block;
}

.flower4 {
  position: absolute;
  right: -200px;
  bottom: 0;
  width: 97px;
  display: block;
}

.flower5 {
  position: absolute;
  right: -200px;
  bottom: -950px;
  width: 99px;
  display: block;
}

.flower6 {
  position: absolute;
  left: -25px;
  bottom: -1030px;
  width: 84px;
  display: block;
}

.triangle {
  position: relative;
  left: 70%;
  width: 0;
  height: 0;
  border-top: 26px solid #ffc000;
  border-right: 40px solid transparent;
  border-left: 40px solid transparent;
}

.triangleAverage {
  left: 50%;
  margin-left: -40px;
}

.mar1 {
  margin-top: 1180px;
}

.mar2 {
  margin-top: 45px;
  margin-bottom: 45px;
}